// Blinking Cursor
.cursor {
    display: inline-block;
    width: 10px;
    height: 20px;
    background-color: var(--accent);
    animation: blink 1s infinite;
    vertical-align: text-bottom;
}

@keyframes blink {
    0%, 50% { opacity: 1; }
    51%, 100% { opacity: 0; }
}

// Spectrum Visualizer
.spectrum {
    height: 60px;
    display: flex;
    align-items: flex-end;
    gap: 2px;
    margin: 20px 0;
    padding: 10px;
    background-color: var(--bg-dark);
    border: 1px solid var(--border);
}

.spectrum-bar {
    flex: 1;
    background-color: var(--text-primary);
    min-height: 2px;
    animation: pulse 2s ease-in-out infinite;
    
    @for $i from 1 through 20 {
        &:nth-child(#{$i}) {
            animation-delay: #{$i * 0.05}s;
            --height: #{($i * 4) + 20}%;
        }
    }
}

@keyframes pulse {
    0%, 100% { 
        height: 10%; 
        opacity: 0.5; 
    }
    50% { 
        height: var(--height, 50%); 
        opacity: 1; 
    }
}

// Terminal Fade In
.terminal-window {
    animation: fadeIn 0.5s ease-out;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

// Typing Effect
@keyframes typing {
    from { width: 0; }
    to { width: 100%; }
}

.typing-effect {
    overflow: hidden;
    white-space: nowrap;
    animation: typing 3s steps(40, end);
}